import React from "react";
import { Link } from "gatsby";

const MyLatestProjectTile = () => {

    return (
        <section className="myLatestProjectTile">
            <div className="myLatestLeft">
                <p className="myLatestText">
                    My <br />
                    Latest <br />
                    Project <br />
                </p>
            </div>
            <div className="myLatestRight">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="30" height="60">
                    <path fill="none" d="M0 0h24v24H0z"/><path d="M13.172 12l-4.95-4.95 1.414-1.414L16 12l-6.364 6.364-1.414-1.414z" fill="#FFF"/>
                </svg>
            </div>
        </section>
    );
}

export default MyLatestProjectTile;