import React from "react";
import { getImage, GatsbyImage } from "gatsby-plugin-image";
import { Link } from "gatsby";

const ServicesSectionMobile = (props) => {

    const image = getImage(props.backgroundImage);

    return (
        <>
            <section className="servicesSectionMobile">
                <div className="servicesSectionMobileOverlay"></div>
                <GatsbyImage image={image} alt="Computer Image" className="mobileComputerImage" />
                <section className="servicesSectionMobileLeft">
                    <p className="servicesSectionMobileHeader">Looking For A Site?</p>
                    <Link to="/services"><div className="servicesSectionMobileButton">View Services</div></Link>
                </section>
                <section className="servicesSectionMobileRight">
                    <ul className="servicesSectionMobileValues">
                        <li className="servicesSectionMobileValue">Reliable</li>
                        <li className="servicesSectionMobileValue">Business Forward</li>
                        <li className="servicesSectionMobileValue">Great Experience</li>
                    </ul>
                </section>
            </section>
        </>
    );
}

export default ServicesSectionMobile;