import React, { useEffect } from "react"
import { graphql } from "gatsby";

import NavBar from "../components/navbar"
import ServicesTile from "../components/homeComponents/servicesTile"
import MyLatestProjectTile from "../components/homeComponents/myLatestProjectTile";
import LatestProject from "../components/homeComponents/latestProject";
import CenterPiece from "../components/homeComponents/centerPiece";
import MyCoreValues from "../components/homeComponents/myCoreValues";
import MoreProjects from "../components/homeComponents/moreProjects";
import ContactInfo from "../components/homeComponents/contactInfo";
import MoreAboutMe from "../components/homeComponents/moreAboutMe";
import MobileNav from "../mobileComponents/mobileNav";
import ServicesSectionMobile from "../mobileComponents/mobileHome/servicesSectionMobile";
import ProjectsSectionMobile from "../mobileComponents/mobileHome/projectsSectionMobile";
import AboutSectionMobile from "../mobileComponents/mobileHome/aboutSectionMobile";
import MobileFooter from "../mobileComponents/mobileFooter";
import "../components/generalStatic.css";
import "../components/homeComponents/servicesTile.css";
import "../components/homeComponents/myLatestProjectTile.css";
import "../components/homeComponents/latestProject.css";
import "../components/homeComponents/centerPiece.css";
import "../components/homeComponents/myCoreValues.css";
import "../components/homeComponents/moreProjects.css";
import "../components/homeComponents/contactInfo.css";
import "../components/homeComponents/moreAboutMe.css";
import "../mobileComponents/mobileHome/servicesSectionMobile.css";
import "../mobileComponents/mobileHome/projectsSectionMobile.css";
import "../mobileComponents/mobileHome/aboutSectionMobile.css";
import "../mobileComponents/mobileFooter.css";

const IndexPage = ({ data }) => {

  useEffect(() => {
    const element = document.getElementById("homeBullet");
    element.classList.add("filled");
  })

  return (
    <>
      <NavBar />
      
      <ServicesTile backgroundImage={data.computerImage} />
      <MyLatestProjectTile />
      <LatestProject backgroundImage={data.matthewSite} />
      <CenterPiece backgroundImage={data.siteLogo} />
      <MyCoreValues />
      <MoreProjects />
      <ContactInfo />
      <MoreAboutMe backgroundImage={data.golfCourseBackground}/>

      <MobileNav backgroundImage={data.siteLogoDark} currentPage="Home"/>
      <ServicesSectionMobile backgroundImage={data.computerImage} />
      <ProjectsSectionMobile backgroundImage={data.matthewSite} />
      <AboutSectionMobile backgroundImage={data.golfCourseBackground} />
      <MobileFooter currentPage="Home" backToTop={true} />
      
    </>
  );
}

export const query = graphql`
  query MyQuery {
    computerImage: file(relativeDirectory: {eq: "homeImages"}, name: {eq: "computerImage"})
      {
        childImageSharp {
          gatsbyImageData(formats: [AUTO, WEBP, AVIF], width: 300, placeholder: BLURRED)
        }
      }
    matthewSite: file(relativeDirectory: {eq: "homeImages"}, name: {eq: "Matthew Site Home Page NEWEST"})
      {
        childImageSharp {
          gatsbyImageData(formats: [AUTO, WEBP, AVIF], width: 300, placeholder: BLURRED)
        }
      }
    siteLogo: file(relativeDirectory: {eq: "homeImages"}, name: {eq: "SitesByJackLogo Light No Background"})
      {
        childImageSharp {
          gatsbyImageData(formats: [AUTO, WEBP, AVIF], width: 500, placeholder: BLURRED)
        }
      }
    golfCourseBackground: file(relativeDirectory: {eq: "homeImages"}, name: {eq: "Golf Course Background"})
      {
        childImageSharp {
          gatsbyImageData(width: 500 placeholder: BLURRED formats: [AUTO, WEBP, AVIF])
        }
      }
    siteLogoDark: file(relativeDirectory: {eq: "homeImages"}, name: {eq: "SitesByJackLogo Dark No Background Smaller"})
    {
      childImageSharp {
        gatsbyImageData(width: 500 placeholder: BLURRED formats: [AUTO, WEBP, AVIF])
      }
    }
  }
`

export default IndexPage