import React from "react";
import { Link } from "gatsby";

const MoreProjects = () => {
    return (
        <Link className="overallMoreProjects" to="/projects">
            <section className="moreProjects">
                <div className="moreProjectsTextCont">
                    <h1 className="moreProjectsText">
                        View <br />
                        More <br />
                        Projects <br />
                    </h1>
                </div>
            </section>
        </Link>
    )
}

export default MoreProjects;