import React from "react";
import { Link } from "gatsby";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

const ProjectsSectionMobile = (props) => {

    const image = getImage(props.backgroundImage);

    return (
        <section className="projectsSectionMobile">
            <section className="projectsSectionMobileHeaderCont">
                <p className="projectsSectionMobileHeader">My Latest Project</p>
            </section>
            <section className="projectsSectionMobileProjectsCont">
                <section className="myLatestProjectTileMobile">
                    <div className="myLatestLeftMobile">
                        <p className="myLatestTextMobile">
                            My <br />
                            Latest <br />
                            Project <br />
                        </p>
                    </div>
                    <div className="myLatestRightMobile">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="30" height="60">
                            <path fill="none" d="M0 0h24v24H0z"/><path d="M13.172 12l-4.95-4.95 1.414-1.414L16 12l-6.364 6.364-1.414-1.414z" fill="#404040" />
                        </svg>
                    </div>
                </section>
                <a className="overallLatestProjectMobile" href="https://mkepressurewashing.com/">
                    <section className="latestProjectMobile">
                        <p className="latestProjectTextMobile">MKE Pressure Washing</p>
                        <div className="overlay2Mobile"></div>
                        <GatsbyImage image={image} alt={"mke pressure washing"} className="matthewSiteMobile" />
                    </section>
                </a>
                
                <Link className="overallMoreProjectsMobile" to="/projects">
                    <section className="moreProjectsMobile">
                        <div className="moreProjectsTextContMobile">
                            <h1 className="moreProjectsTextMobile">
                                View <br />
                                More <br />
                                Projects <br />
                            </h1>
                        </div>
                    </section>
                </Link>
            </section>
            
            <section className="projectsSectionMobileButtonCont">
                <Link className="projectsSectionMobileButton" to="/projects">
                    <div className="projectsSectionMobileButtonStylingCont">
                        View More Projects
                    </div>
                </Link>
            </section>
            
        </section>
    );
}

export default ProjectsSectionMobile;