import React from "react";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

const CenterPiece = (props) => {

    const image = getImage(props.backgroundImage);

    return (
        <header className="centerPiece">
            <GatsbyImage image={image} alt="SitesByJack Logo" className="sitesByJackLogo" />
            <h1 className="centerPieceText">SitesByJack</h1>
        </header>
    );
}

export default CenterPiece;