import React from "react";
import { getImage, GatsbyImage } from 'gatsby-plugin-image';
import { Link } from "gatsby";

const MoreAboutMe = (props) => {

    const image = getImage(props.backgroundImage);
    return (
        <Link className="overallMoreAboutMe" to="/about">
            <section className="moreAboutMe">
                <h1 className="moreAboutMeText">Find Out More About Me</h1>
                <div className="moreAboutMeOverlay"></div>
                <GatsbyImage image={image} alt={"golf course"} className="golfBackground" />
            </section>
        </Link>
    )
}

export default MoreAboutMe;