import React from "react";
import { Link } from "gatsby";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

const AboutSectionMobile = (props) => {

    const image = getImage(props.backgroundImage);

    return (
        <section className="aboutSectionMobile">
            <h1 className="aboutSectionMobileHeader">More About Me</h1>
            <Link to="/about"><div className="aboutSectionMobileButton">Click Here</div></Link>
            <div className="aboutSectionMobileOverlay" />
            <GatsbyImage image={image} alt="Golf Course" className="aboutSectionMobileImage" />
        </section>
    );
}

export default AboutSectionMobile;