import React from "react";
import { getImage, GatsbyImage } from 'gatsby-plugin-image';
import { Link } from "gatsby";

const ServicesTile = (props) => {
    
    const image = getImage(props.backgroundImage);

    
    return (
        <Link className="overallServicesTile" to="/services">
            <section className="servicesTile">
                <p className="servicesTileText">Looking For A Site?</p>
                <div className="overlay"></div>
                <GatsbyImage image={image} alt={"testImage"} className="innerImage" />
            </section>
        </Link>
    );
}

export default ServicesTile;