import React from "react";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

const LatestProject = (props) => {
        
    const image = getImage(props.backgroundImage);

    return (
        <a className="overallLatestProject" href="https://mkepressurewashing.com/">
            <section className="latestProject">
                <p className="latestProjectText">MKE Pressure Washing</p>
                <div className="overlay2"></div>
                <GatsbyImage image={image} alt={"mke pressure washing"} className="matthewSite" />
            </section>
        </a>
    );
}

export default LatestProject;